.Movie{
    display:flex;
    place-items: center center;
    height:250px;
    width:350px;
    background-color: #f6f3ed;
    border-radius:5px;
    box-shadow:  0px 0px 9px 2px #000000;
  }
  .Movie__Columns{
    margin-left:7px;
    margin-top: 60px;
    font-size: 13px;
    width:85%;
    height:100%;
  }
  .Movie__Posters{
      margin-left:7px;
  }
  .Movie__image{
    height:200px;
    width:140px;
    box-shadow:  0px 0px 9px 2px #000000;
    border-radius:2px;
  }
  .Movie__description{
    height:120px;
    width:170px;
  }
  .Movie__genre{
    font-size:10px;
    display: flex;
    flex-direction: row;
  }
  .Movie__title{
      font-weight: 100;
      font-size:9px;
  }